<template>
    <div>
        <div>
            <h2 class="title">
                绑定角色权限:
                <Tag color="blue">{{ role.name }}</Tag>
            </h2>
            <Button class="back" type="primary" @click.prevent="$router.go(-1)">
                <Icon type="md-arrow-back"></Icon>
                返回
            </Button>
        </div>
        <Row :gutter="10" class="margin-top-10 margin-bottom-10">
            <i-col :span="span">
                <Card>
                    <Tree ref="menu" :data="menus" :render="renderTree" show-checkbox></Tree>
                </Card>
            </i-col>
            <i-col :span="span" v-if="hasButton">
                <Card>
                    <Tree ref="button" :data="buttons" :render="renderTree" show-checkbox></Tree>
                </Card>
            </i-col>
            <i-col :span="span" v-if="hasApi">
                <Card>
                    <Tree ref="api" :data="apis" :render="renderTree" show-checkbox></Tree>
                </Card>
            </i-col>
        </Row>
        <Button type="success" long @click="save">保存</Button>
    </div>
</template>
<script>
import { MakeTree } from '@/libs/common';
import { Tag } from 'iview';
export default {
    name: 'RoleBindPermission',
    data () {
        return {
            role: {
                id: this.$route.query.id,
                name: this.$route.query.name
            },
            menus: [],
            buttons: [],
            apis: []
        };
    },
    computed: {
        hasButton () {
            return $config.PERMISSION_BUTTON;
        },
        hasApi () {
            return $config.PERMISSION_API;
        },
        span () {
            return this.hasButton && this.hasApi ? 8 : this.hasButton || this.hasApi ? 12 : 24;
        }
    },
    async created () {
        await this.load();
    },
    methods: {
        initChecked (array, permissions) {
            array.forEach(item => {
                if (item.children && item.children.length) {
                    return this.initChecked(item.children, permissions);
                }
                if (permissions.some(v => v.id === item.id)) {
                    item.checked = true;
                }
            });
        },
        async load () {
            const roleResult = await this.get(`/api/permission/role/permission/list/${this.role.id}`);
            if (roleResult.success) {
                const rolePermissions = roleResult.value;
                const allResult = await this.get('/api/permission/list');
                if (allResult.success) {
                    const allPermission = allResult.value;
                    let ms = allPermission.filter(p => p.type === 1);
                    ms = MakeTree(ms, 0, 'parent');
                    ms.forEach(m => m.expand = true);
                    this.initChecked(ms, rolePermissions);
                    this.menus = ms;

                    if (this.hasButton) {
                        let btns = allPermission.filter(p => p.type !== 3);
                        btns = MakeTree(btns, 0, 'parent');
                        btns.forEach(b => b.expand = true);
                        this.initChecked(btns, rolePermissions);
                        this.buttons = btns;
                    }

                    if (this.hasApi) {
                        const apis = allPermission.filter(p => p.type === 3);
                        apis.filter(p => rolePermissions.some(v => v.id === p.id)).forEach(p => p.checked = true);
                        this.apis = apis;
                    }
                }
            }
        },
        renderTree (h, { root, node, data }) {
            return (
                <span style="display:inline-block; width: 100%">
                    <span>{ data.name }</span>
                    <span class="margin-left-10" style="font-size: 14px; color: #909399">{ data.value }</span>
                    { data.type !== 3 ? <Tag class="margin-left-10" color={ data.type === 1 ? 'green' : 'orange' }>{ data.type === 1 ? '菜单' : '按钮' }</Tag> : '' }
                </span>
            );
        },
        async save () {
            const menus = this.$refs.menu.getCheckedAndIndeterminateNodes();
            const buttons = this.hasButton ? this.$refs.button.getCheckedAndIndeterminateNodes() : [];
            const apis = this.hasApi ? this.$refs.api.getCheckedAndIndeterminateNodes() : [];
            const permissions = [...menus, ...buttons.filter(b => b.type === 2), ...apis];
            const result = await this.post(`/api/permission/role/bind/${this.role.id}`, permissions.map(p => p.id));
            if (result.success) {
                this.$Notice.success({ title: '绑定权限资源成功' });
                await this.load();
            }
        }
    }
};
</script>
<style scoped>
.title {
    display: inline-block;
}
.back {
    float: right;
}
</style>
